@import "../../../vars.scss";

.question-answer {
    margin: 50px auto;
    max-width: 800px;

    &__question {
        font-size: 36px;
        margin: 20px;
        letter-spacing: 4px;
    }

    &__answer {
        font-family: $font-secondary;
        font-size: 18px;
    }

    a {
        color: gold;
              
        &:visited {
            color: gold;
        }
      
        &:hover, &:focus {
          text-decoration: none;
        }
    }
}

